import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useCallback, useEffect, useState } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';
// material
import { Box, Card, Grid, Stack, TextField } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import Request from '../../../utils/Request';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
//
import { useDispatch, useSelector } from '../../../redux/store';
import { getTirageConfs } from '../../../redux/slices/user';
import useAuth from '../../../hooks/useAuth';
import { handlePermission } from '../../../utils/handlePermission';
// ----------------------------------------------------------------------

TirageConfNewForm.propTypes = {
  currentTirageConf: PropTypes.object
};

export default function TirageConfNewForm({ currentTirageConf }) {
  const navigate = useNavigate();
  const { user } = useAuth();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const NewTirageConfSchema = Yup.object().shape({
    pick2: Yup.string().nullable().length(2),
    lotto_3_box: Yup.string().nullable().length(2),
    tirage_1: Yup.number().required('tirage_1 is required'),
    tirage_2: Yup.number().required('tirage_2 is required'),
    tirage_3: Yup.number().required('tirage_3 is required'),
    mariage1: Yup.number().required('mariage1 is required'),
    lotto_3: Yup.number().required('lotto_3 is required'),
    lotto_4_1: Yup.number().required('lotto_4 1 is required'),
    lotto_4_2: Yup.number().required('lotto_4 2 is required'),
    lotto_4_3: Yup.number().required('lotto_4 3 is required'),
    lotto_5_1: Yup.number().required('lotto_5 1 is required'),
    lotto_5_2: Yup.number().required('lotto_5 2 is required'),
    lotto_5_3: Yup.number().required('lotto_5 3 is required'),
    boule_paire: Yup.number().required('boule paire is required'),
    boule_paire_l3: Yup.number().required('boule paire lotto 3 is required'),
    mariage_free: Yup.number().required('mariage free is required')
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      pick2: currentTirageConf?.pick2 || null,
      lotto_3_box: currentTirageConf?.lotto_3_box || null,
      tirage_1: currentTirageConf?.tirage_1 || null,
      tirage_2: currentTirageConf?.tirage_2 || null,
      tirage_3: currentTirageConf?.tirage_3 || null,
      mariage1: currentTirageConf?.mariage1 || null,
      mariage2: currentTirageConf?.mariage2 || null,
      mariage3: currentTirageConf?.mariage3 || null,
      lotto_3: currentTirageConf?.lotto_3 || null,
      lotto_4_1: currentTirageConf?.lotto_4_1 || null,
      lotto_4_2: currentTirageConf?.lotto_4_2 || null,
      lotto_4_3: currentTirageConf?.lotto_4_3 || null,
      lotto_5_1: currentTirageConf?.lotto_5_1 || null,
      lotto_5_2: currentTirageConf?.lotto_5_2 || null,
      lotto_5_3: currentTirageConf?.lotto_5_3 || null,
      boule_paire_l3: currentTirageConf?.boule_paire_l3 || null,
      boule_paire: currentTirageConf?.boule_paire || null,
      mariage_free: currentTirageConf?.mariage_free || null
    },
    validationSchema: NewTirageConfSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      try {
        await Request.handleUpdatePrimeConf(values);
        resetForm();
        setSubmitting(false);
        enqueueSnackbar('Mettre à jour avec succès', { variant: 'success' });
        setTimeout(() => {
          window.location.reload();
        }, 1000);
      } catch (error) {
        if (error?.message) {
          enqueueSnackbar(error?.message, { variant: 'error' });
        } else {
          alert(JSON.stringify(error));
        }
        setSubmitting(false);
        setErrors(error);
      }
    }
  });

  useEffect(() => {
    dispatch(getTirageConfs());
  }, [dispatch]);

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={8}>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Pick 2"
                    placeholder="Entrer le montant du pick 2"
                    {...getFieldProps('pick2')}
                    error={Boolean(touched.pick2 && errors.pick2)}
                    // helperText={touched.pick2 && errors.pick2}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Lotto 3 box"
                    placeholder="Entrer le montant du lotto 3 box"
                    {...getFieldProps('lotto_3_box')}
                    error={Boolean(touched.lotto_3_box && errors.lotto_3_box)}
                    // helperText={touched.lotto_3_box && errors.lotto_3_box}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Tirage 1"
                    placeholder="Entrer le montant du tirage 1"
                    {...getFieldProps('tirage_1')}
                    error={Boolean(touched.tirage_1 && errors.tirage_1)}
                    // helperText={touched.tirage_1 && errors.tirage_1}
                  />
                </Stack>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Tirage 2"
                    placeholder="Entrer le montant du tirage 2"
                    {...getFieldProps('tirage_2')}
                    error={Boolean(touched.tirage_2 && errors.tirage_2)}
                    // helperText={touched.tirage_2 && errors.tirage_2}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Tirage 3"
                    placeholder="Entrer le montant du tirage 3"
                    {...getFieldProps('tirage_3')}
                    error={Boolean(touched.tirage_3 && errors.tirage_3)}
                    // helperText={touched.tirage_3 && errors.tirage_3}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label={user?.mariage_option ? 'Mariage op1' : 'Mariage'}
                    placeholder={
                      user?.mariage_option ? 'Entrer le montant du mariage op1' : 'Entrer le montant du mariage'
                    }
                    {...getFieldProps('mariage1')}
                    error={Boolean(touched.mariage1 && errors.mariage1)}
                    // helperText={touched.mariage1 && errors.mariage1}
                  />
                </Stack>
                {user?.mariage_option && (
                  <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                    <TextField
                      disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                      fullWidth
                      label="Mariage op2"
                      placeholder="Entrer le montant du Mariage op2"
                      {...getFieldProps('mariage2')}
                      error={Boolean(touched.mariage2 && errors.mariage2)}
                      // helperText={touched.mariage2 && errors.mariage2}
                    />
                    <TextField
                      disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                      fullWidth
                      label="Mariage op3"
                      placeholder="Entrer le montant du Mariage op3"
                      {...getFieldProps('mariage3')}
                      error={Boolean(touched.mariage3 && errors.mariage3)}
                      // helperText={touched.mariage3 && errors.mariage3}
                    />
                  </Stack>
                )}
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Lotto 3"
                    placeholder="Entrer le montant du lotto 3"
                    {...getFieldProps('lotto_3')}
                    error={Boolean(touched.lotto_3 && errors.lotto_3)}
                    // helperText={touched.lotto_3 && errors.lotto_3}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Lotto 4 op1"
                    placeholder="Entrer le montant du lotto 4 op1"
                    {...getFieldProps('lotto_4_1')}
                    error={Boolean(touched.lotto_4_1 && errors.lotto_4_1)}
                    // helperText={touched.lotto_4_1 && errors.lotto_4_1}
                  />
                </Stack>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Lotto 4 op2"
                    placeholder="Entrer le montant du lotto 4 op2"
                    {...getFieldProps('lotto_4_2')}
                    error={Boolean(touched.lotto_4_2 && errors.lotto_4_2)}
                    // helperText={touched.lotto_4_2 && errors.lotto_4_2}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Lotto 4 op3"
                    placeholder="Entrer le montant du lotto 4 op3"
                    {...getFieldProps('lotto_4_3')}
                    error={Boolean(touched.lotto_4_3 && errors.lotto_4_3)}
                    // helperText={touched.lotto_4_3 && errors.lotto_4_3}
                  />
                </Stack>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Lotto 5 op1"
                    placeholder="Entrer le montant du lotto 5 op1"
                    {...getFieldProps('lotto_5_1')}
                    error={Boolean(touched.lotto_5_1 && errors.lotto_5_1)}
                    // helperText={touched.lotto_5_1 && errors.lotto_5_1}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Lotto 5 op2"
                    placeholder="Entrer le montant du lotto 5 op2"
                    {...getFieldProps('lotto_5_2')}
                    error={Boolean(touched.lotto_5_2 && errors.lotto_5_2)}
                    // helperText={touched.lotto_5_2 && errors.lotto_5_2}
                  />
                </Stack>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Lotto 5 op3"
                    placeholder="Entrer le montant du lotto 5 op3"
                    {...getFieldProps('lotto_5_3')}
                    error={Boolean(touched.lotto_5_3 && errors.lotto_5_3)}
                    // helperText={touched.lotto_5_3 && errors.lotto_5_3}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="Mariage gratuit"
                    placeholder="Entrer le montant du mariage gratuit"
                    {...getFieldProps('mariage_free')}
                    error={Boolean(touched.mariage_free && errors.mariage_free)}
                    // helperText={touched.mariage_free && errors.mariage_free}
                  />
                </Stack>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="boule paire"
                    placeholder="Entrer le montant du boule paire"
                    {...getFieldProps('boule_paire')}
                    error={Boolean(touched.boule_paire && errors.boule_paire)}
                    // helperText={touched.boule_paire && errors.boule_paire}
                  />
                  <TextField
                    disabled={!handlePermission(user?.perms, 'change_tauxtirage')}
                    fullWidth
                    label="boule paire lotto 3"
                    placeholder="Entrer le montant du boule paire lotto 3"
                    {...getFieldProps('boule_paire_l3')}
                    error={Boolean(touched.boule_paire_l3 && errors.boule_paire_l3)}
                    // helperText={touched.boule_paire_l3 && errors.boule_paire_l3}
                  />
                </Stack>

                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                  <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                    Sauvegarder les modifications
                  </LoadingButton>
                </Box>
              </Stack>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
