import { useEffect } from 'react';
import { paramCase } from 'change-case';
import { useParams, useLocation, Link as RouterLink } from 'react-router-dom';
// material
import { Icon } from '@iconify/react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { Container, Button, Stack } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getPrimeConfs } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import TirageConfNewForm from '../../components/_dashboard/tirageConf/TirageConfNewForm';

// ----------------------------------------------------------------------

export default function TirageConfCreate() {
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { primeConfs } = useSelector((state) => state.user);

  useEffect(() => {
    dispatch(getPrimeConfs());
  }, [dispatch]);

  return (
    <Page title="Configuration tirage: Modifier la configuration | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Modifier prime generale"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Prime generale', href: PATH_DASHBOARD.tirage?.configuration },
            { name: 'Modification' }
          ]}
        />

        {primeConfs?.tirage_1 && <TirageConfNewForm currentTirageConf={primeConfs} />}
      </Container>
    </Page>
  );
}
